.header {
    display: flex;
    position: absolute;
    transition: 300ms all;
    background-color: #6ab04c;
    height: 5rem;
    z-index: 5;
    top: 1rem;
    margin: 0 5.5vw;
    width: 89vw;
    .header--inner {
        margin: auto auto !important;
        display: flex;
        position: relative;
        height: auto;
        align-items: center;
        margin-left: 3vw;

        .header--img {
            width: 12rem;
            height: auto;
        }
    }
}
.header--breadcrumbs{
    margin-left: 5.5vw;
}
.header--shut {
    width: 100% !important;
    margin: 0 0 !important;
    position: fixed;
    top: 0 !important;
    transition: 0.5s all;
}
.header--nav {
    .breadcrumbs{
        padding-left: 0 !important;
    }
    margin-top: 3.2rem;
    padding-top: 4rem;
    background-color: #f6f6f6 ;
    padding-bottom: 2rem;
    .label {
        margin-left: 5.5vw;
        font-size: 1.2rem;
        font-weight: 700;
    }
}
@media (max-width: 768px) {
    .header {
        height: 4.5rem;

        .header--inner {
            .header--img {
                    width: 10rem;
                
            }
        }
    }
    .header--nav {
    padding-top: 4rem;
    }
}
@media (max-width: 560px) {
    .header {
        margin: 0 !important;
        width: 100vw;
        height: 4rem;
        .header--inner {
            .header--img {
                    width: 8rem;
                
            }
        }
        top: 0 !important;
    }
    .header--nav {
        padding-bottom: 1rem;
        padding-top: 5rem;
        margin-top: 0 !important;
        .breadcrumbs{
            padding-left: 0 !important;
        }
        .label {
            margin-left: 2vw;
        }
    .header--breadcrumbs{
        margin-left: 0 !important;
        padding-left: 2vw !important;
    }
    }
}
