.selectric-open {
    z-index: 16;
    box-shadow: $box-shadow;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    background-color: #fff;

    .selectric {
        border-color: transparent;
    }

    .selectric-items {
        display: block;
        background-color: #fff;
        overflow: hidden;
    }
}

.selectric-disabled {
    filter: alpha(opacity=50);
    opacity: 0.5;
    cursor: default;
    user-select: none;
}

.selectric-hide-select {
    position: relative;
    overflow: hidden;
    width: 0;
    height: 0;

    select {
        position: absolute;
        left: -100%;
    }

    &.selectric-is-native {
        position: absolute;
        width: 100%;
        height: 100%;
        z-index: 10;

        select {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            height: 100%;
            width: 100%;
            border: none;
            z-index: 1;
            box-sizing: border-box;
            opacity: 0;
        }
    }
}

.selectric-input {
    position: absolute !important;
    top: 0 !important;
    left: 0 !important;
    overflow: hidden !important;
    clip: rect(0, 0, 0, 0) !important;
    margin: 0 !important;
    padding: 0 !important;
    width: 1px !important;
    height: 1px !important;
    outline: none !important;
    border: none !important;
    *font: 0/0 a !important;
    background: none !important;
}

.selectric-temp-show {
    position: absolute !important;
    visibility: hidden !important;
    display: block !important;
}

/* Items box */
.selectric-items {
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    z-index: -1;
    box-shadow: 0 0 10px -6px;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;

    .selectric-scroll {
        height: 100%;
        overflow: auto;
    }

    .selectric-above & {
        //top: auto;
        //bottom: 100%;
    }

    ul,
    li {
        list-style: none;
        padding: 0;
        margin: 0 !important;
        font-size: $selectric-font-size;
        line-height: 20px;
        min-height: 20px;
    }

    li {
        display: block;
        padding: 10px 10px 10px 40px;
        color: $black;
        text-transform: uppercase;
        font-weight: 700;
        font-size: 11px;
        cursor: pointer;

        &.selected {
            background: #f8f8f8;
            color: $primary-color;
        }

        &.highlighted {
            background: #f8f8f8;
            color: $primary-color;
        }

        &:hover {
            background: #f8f8f8;
            color: $primary-color;
        }
    }

    .disabled {
        filter: alpha(opacity=50);
        opacity: 0.5;
        cursor: default !important;
        background: none !important;
        color: #666 !important;
        user-select: none;
    }

    .selectric-group {
        .selectric-group-label {
            font-weight: bold;
            padding-left: 10px;
            cursor: default;
            user-select: none;
            background: none;
            color: #444;
        }

        &.disabled li {
            filter: alpha(opacity=100);
            opacity: 1;
        }

        li {
            padding-left: 25px;
        }
    }
}
