@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap');
body{
    
        font-family: "Montserrat",sans-serif;
}
.order__content{
    display: grid;
    grid-template-columns: minmax(10rem, 2fr) minmax(20rem, 4fr);
    gap: 1rem;
    margin: 3rem 2rem 2rem; 
    
    small{
        font-weight: 700;
        font-size: 0.8rem !important;
    }
    .label--sm{
        display: flex;
        margin-bottom: 3vw;
        font-size: 1rem;
        color: #9b9b9b;
    }
    .label--mar{
        margin: 2vw 0 !important;
    }
    .label--left{
        font-weight: 700;
    }
    .textarea{
        margin: 1rem 2rem 4vw 0 !important; 
        width: 80%;
    }
}

.order__info_comment{
    margin: 2rem 0 !important;
.label{
        margin: 1rem 0 !important;
    }
    .input{

        margin: 1rem 0  0!important;
    }
}
.order__content{
    .selectric , li{
        font-size: 0.8rem !important;
        text-transform: none !important;
    }
}
.form__order__info{

    .btn {
        margin: 1vw 0 5vw;
    }
    
    width: 18rem;
    .order__info__item{
        max-height: 3rem;
        margin: 1rem 0;
        display: flex;

        .item__add, .item__remove{
            cursor: pointer;
            color: #fff;
            position: relative;
            background-color: #6ab04c;
            padding: 1px 8px 4px 7px;
            border-radius: 50%;
            font-weight: 900;
            
        }
        .item__add{
            top: .2rem;
            left: 4rem;
            transform: rotateZ(90deg);
        }
        .item__remove{
            top: 0.8rem;
            font-weight: 900;
            left: 4rem;
            transform: rotateZ(-90deg);
        }

        .order__info__item__count{
            width:2.3rem;
            .input{
                margin-left: 1rem;
            }
        }
    }
    .order__info_comment{
        margin: 1rem 0;
        width: 17.5rem;
    }
}


@media (min-width: 1080px) {
    .order__content{
    margin: 3rem auto 2rem !important; 
    max-width: 1000px !important;
    }
}

@media (max-width: 768px) {
     .order__content{
        margin-left: 3vw;
        grid-template-columns: 8.7rem 1fr;
    }
    .order__info__item{
        .item__add{
            top: 0.1rem !important
        }
        .item__remove{
            top: 0.4rem !important
        }
    }
}
@media (max-width: 560px) {
    .order__content{
        // overflow:visible !important;
          width: 98% !important;
          margin-left: 2vw !important;
          margin-right: 2vw !important;
        grid-template-columns: 1fr !important;
    }
}