
$primary-color: #6ab04c;
button,
.btn {
	position: relative;
	display: inline-block;
	border-radius: 0;
	border: 0;
	transition: .3s all;
	background-color: transparent;
	font-family: inherit;
	font-size: 14px;
	text-align: center;
	vertical-align: middle;

	> * {
		position: relative;
	}

	&:before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		border-radius: 100px;

		height: 0;
		//transform: scaleY(0);
		transform-origin: center top;
		//transition: transform .25s ease;
		transition: all .25s ease;
	}

	&.is-loading {
		&:after {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			border-radius: 100px;
			// background-image: url('../img/preloader/preloader-main-gray.gif');
			background-size: 30px 30px;
			background-repeat: no-repeat;
			background-position: center center;
			background-color: inherit;
			z-index: 2;
		}
	}
}

.btn-primary {
	background-color: #6ab04c;
	color: #fff;
	text-transform: uppercase;
	font-size: 14px;
	font-weight: 700;
	padding: 16px 40px;
	border-radius: 100px;
	overflow: hidden;

	&:before {
		background-color: #6fbe4d;
	}

	&:hover {

		@media (min-width: 768px) {
			&:before {
				//transform: scaleY(1);
				height: 100%;
			}
		}
	}

	&.is-loading {
		&:after {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			border-radius: 100px;
			// background-image: url('../img/preloader/preloader-main-white.gif');
			background-size: 30px 30px;
			background-repeat: no-repeat;
			background-position: center center;
			background-color: inherit;
			z-index: 2;
		}
	}

	span {
		position: relative;
	}
}

.btn-uppercase {
	text-transform: uppercase;
}

.btn-transparent-icon {
	font-size: 12px;
	color: #6ab04c;
	border: 1px solid #eeeeee;
	border-radius: 100px;
	padding: 10px 18px 8px 13px;
	white-space: nowrap;
	overflow: hidden;

	&:hover {
		@media (min-width: 768px) {
			border-color: #f6f6f6;

			&:before {
				//transform: scaleY(1);
				height: 100%;
			}
		}
	}

	&:before {
		background-color: #f6f6f6;
	}

	.icon-envelope {
		width: 24px;
		height: 21px;
	}

	.icon-letter {
		display: inline-block;
		width: 24px;
		height: 21px;
		// background-image: url('../img/icons/envelope.svg');
		vertical-align: middle;
		margin-right: 2px;
	}
}


[data-logIn] {
	white-space: nowrap;

	span {
		pointer-events: none;
	}

	.icon-user {
