		width: 50px;
	}
}

.btn-nav-catalog {
	color: #fff;
	text-align: center;
	font-weight: 500;
	white-space: nowrap;
	display: flex;
	align-items: center;

	&:hover {
		@media (min-width: 768px) {
			.icon-burger span:nth-child(2) {
				width: 20px;
			}
		}
	}

	@media (max-width: 768px) {
		display: flex;
		flex-flow: column;
		justify-content: center;
		height: 60px;
		width: 81px;
		padding-left: 20px;
		align-items: flex-start;
	}

	@media (max-width: 420px) {
		width: 81px;
		height: 40px;
		padding-left: 0;
		justify-content: center;
		align-items: center;
	}

	&.is-active .icon-burger {

		@media (max-width: 768px) {
			span:nth-child(2) {
				opacity: 0;
			}

			span:nth-child(1) {
				transform: rotate(45deg) translate(4px, 4px);
			}
			span:nth-child(3) {
				transform: rotate(-45deg) translate(4px, -5px);
			}
		}
	}

	.icon-burger {
		margin-right: 15px;
		pointer-events: none;

		@media (max-width: 768px) {
			margin-right: 0;
		}

		span {
			display: block;
			background-color: #fff;
			height: 2px;

			@media (max-width: 768px) {
				transition: .3s all;
			}
		}

		span:first-child {
			width: 20px;
		}

		span:nth-child(2) {
			width: 12px;
			margin-top: 4px;
			transition: .3s all;

			@media (max-width: 768px) {
				width: 20px;
			}
		}

		span:last-child {
			display: none;
			margin-top: 4px;

			@media (max-width: 768px) {
				display: block;
			}
		}
	}

	.text {

		@media (max-width: 768px) {
			display: none;
		}
	}
}

.btn-basket {
	padding-left: 30px;
	padding-right: 10px;
	font-weight: 500;
	color: #fff;

	@media (min-width: 1120px) {
		padding-left: 48px;
	}

	&:hover {
		@media (min-width: 768px) {
			.btn-icon:before {
				opacity: 1;
				transform: scale(1);
				background-color: #85c469;
			}
		}
	}

	@media (max-width: 768px) {
		display: flex;
		align-items: center;
	}

	@media (max-width: 420px) {
		padding-left: 0;
	}

	span {
		pointer-events: none;
	}

	.btn-icon {
		position: relative;

		&:after {
			content: '';
			height: 200%;
			position: absolute;
			top: -50%;
			left: -50%;
			width: 200%;
		}

		&:before {
			content: '';
			position: absolute;
			top: 50%;
			left: 50%;
			transform: scale(0);
			width: 50px;
			height: 50px;
			border-radius: 100%;
			opacity: 0;
			background-color: transparent;
			margin-top: -25px;
			margin-left: -25px;
			transition-duration: .15s;
			transition-timing-function: cubic-bezier(0.4, 0.0, 0.2, 1);
			transition-property: transform, opacity;
		}

		svg {
			position: relative;
		}

		.badge {
			position: absolute;
			top: -11px;
			right: -20px;
			font-size: 10px;
			font-weight: 600;
			color: #fff;
			background-color: #000;
			border-radius: 100%;
			padding: 2px 4px;
			min-width: 18px;
			min-height: 18px;
			text-align: center;
			animation: pulse .7s forwards ease;
		}
	}

	.btn-icon svg {
		width: 20px;
		height: 22px;
		fill: #fff;
	}

	.price {
		margin-left: 27px;

		@media (max-width: 768px) {
			display: none;
		}
	}
}

