	justify-content: center;

	&:hover {

		svg {
			fill: $black;
		}
	}

	svg {
		vertical-align: middle;
		width: 12px;
		height: 12px;
		transition: .3s all;
		fill: #cfcfcf;
	}
}

.btn-change-vision {
	width: 85px;
	height: 60px;

	&.is-active {

		.state.state--default {
			opacity: 0;
			visibility: hidden;
		}

		.state.state--active {
			opacity: 1;
			visibility: visible;
		}
	}

	.state {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		display: block;
		margin-top: 4px;
		transition: .3s all;
		opacity: 0;
		visibility: hidden;

		@media (max-width: 420px) {
			margin-top: 7px;
		}

		&.state--default {
			opacity: 1;
			visibility: visible;
		}
	}

	.icon-unvision {
		width: 26px;
		height: 8px;
	}

	.icon-vision {
		width: 24px;
		height: 16px;
	}
}

[data-close-modal] span {
	pointer-events: none;
}

@keyframes pulse {
	0% {
		transform: scale(1);
	}

	50% {
		transform: scale(1.4);
	}

	100% {
		transform: scale(1);
	}
}
